import Link from 'next/link';
import { prisma } from '@/lib/prisma';
import { Search, Map as MapIcon, Plus, History, TrendingUp, Activity } from 'lucide-react';
import { Card, Button, Badge } from '@/components/ui';

export default async function DashboardPage() {
    const scansCount = await prisma.scan.count();
    const recentScans = await prisma.scan.findMany({
        orderBy: { createdAt: 'desc' },
        take: 5,
    });

    return (
        <div className="p-8 max-w-7xl mx-auto space-y-8">
            <header className="flex justify-between items-end">
                <div>
                    <h2 className="text-3xl font-bold text-white">Dashboard</h2>
                    <p className="text-zinc-400 mt-1">Overview of your local SEO tracking performance</p>
                </div>
                <Link href="/scans/new">
                    <Button>
                        <Plus size={18} />
                        New Scan
                    </Button>
                </Link>
            </header>

            {/* Metrics Grid */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
                <Card className="space-y-2">
                    <div className="flex items-center gap-2 text-zinc-400 text-sm font-medium">
                        <Activity size={16} /> Total Scans
                    </div>
                    <p className="text-3xl font-bold text-white">{scansCount}</p>
                </Card>
                <Card className="space-y-2">
                    <div className="flex items-center gap-2 text-zinc-400 text-sm font-medium">
                        <MapIcon size={16} /> Grid Points
                    </div>
                    <p className="text-3xl font-bold text-white">--</p>
                </Card>
                <Card className="space-y-2">
                    <div className="flex items-center gap-2 text-zinc-400 text-sm font-medium">
                        <TrendingUp size={16} /> Avg. Visibility
                    </div>
                    <p className="text-3xl font-bold text-emerald-400">Top 3</p>
                </Card>
                <Card className="space-y-2">
                    <div className="flex items-center gap-2 text-zinc-400 text-sm font-medium">
                        <History size={16} /> Active Trackers
                    </div>
                    <p className="text-3xl font-bold text-blue-400">0</p>
                </Card>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                {/* Recent Activity */}
                <div className="lg:col-span-2 space-y-6">
                    <h3 className="text-xl font-bold text-white">Recent Scans</h3>
                    <div className="space-y-4">
                        {recentScans.map((scan) => (
                            <Link key={scan.id} href={`/scans/${scan.id}`}>
                                <div className="group bg-zinc-900/30 border border-zinc-800/50 hover:bg-zinc-800/50 hover:border-zinc-700 p-4 rounded-xl transition-all flex items-center justify-between">
                                    <div className="flex items-center gap-4">
                                        <div className="p-3 bg-zinc-800 rounded-lg text-zinc-400 group-hover:text-white transition-colors">
                                            <Search size={20} />
                                        </div>
                                        <div>
                                            <h4 className="font-semibold text-zinc-200 group-hover:text-white">{scan.keyword}</h4>
                                            <p className="text-sm text-zinc-500">
                                                {new Date(scan.createdAt).toLocaleDateString()} • {scan.gridSize}x{scan.gridSize} Grid
                                            </p>
                                        </div>
                                    </div>
                                    <div className="flex items-center gap-4">
                                        <Badge color={
                                            scan.status === 'COMPLETED' ? 'green' :
                                                scan.status === 'RUNNING' ? 'blue' :
                                                    'gray'
                                        }>
                                            {scan.status}
                                        </Badge>
                                    </div>
                                </div>
                            </Link>
                        ))}
                        {recentScans.length === 0 && (
                            <div className="text-center py-12 text-zinc-500 bg-zinc-900/20 rounded-xl border border-dashed border-zinc-800">
                                No scans yet. Start your first one!
                            </div>
                        )}
                    </div>
                </div>

                {/* Quick Actions / Tips */}
                <div className="space-y-6">
                    <h3 className="text-xl font-bold text-white">Quick Actions</h3>
                    <Card className="space-y-4">
                        <div className="bg-gradient-to-br from-blue-500/20 to-purple-500/20 p-4 rounded-xl border border-blue-500/10">
                            <h4 className="font-semibold text-blue-100 mb-1">Schedule a Tracker</h4>
                            <p className="text-sm text-blue-200/60 mb-3">Set up daily or weekly scans to monitor ranking changes over time.</p>
                            <Link href="/scans/new">
                                <Button variant="outline" className="w-full text-xs py-2 h-auto">
                                    Create Schedule
                                </Button>
                            </Link>
                        </div>
                    </Card>
                </div>
            </div>
        </div>
    );
}
